/**
 * 作業步驟ViewModel
 *
 * @public
 * @class JobStepViewModel
 */
export class JobStepViewModel {
  /// <summary>
  ///  作業步驟編號
  /// <summary>
  public job_step_id: string;
  /// <summary>
  ///  SSIS作業編號
  /// <summary>
  public ssis_job_no: string;
  /// <summary>
  ///  程式功能編號
  /// <summary>
  public program_func_no: string;

  /// <summary>
  ///  程式功能說明
  /// <summary>
  public program_func_desc: string;

  /// <summary>
  ///  轉檔程式類別
  /// <summary>
  public program_type: string;
  /// <summary>
  ///  步驟
  /// <summary>
  public step: number;
  /// <summary>
  ///  成功後步驟
  /// <summary>
  public success_step: number;
  /// <summary>
  ///  失敗後步驟
  /// <summary>
  public fail_step: number;
  /// <summary>
  ///  最後執行狀態主鍵
  /// <summary>
  public last_exec_status_key: string;
  /// <summary>
  ///  最後執行時間
  /// <summary>
  public last_exec_date: Date;

  /// <summary>
  ///  啟用註記
  /// <summary>
  public is_active: boolean;
  /// <summary>
  ///  建立者
  /// <summary>
  public creator: string;
  /// <summary>
  ///  建立時間
  /// <summary>
  public create_time: Date;
  /// <summary>
  ///  最後更新者
  /// <summary>
  public modifier: string;
  /// <summary>
  ///  最後更新時間
  /// <summary>
  public last_update_time: Date;

  public last_exec_status: string;
  public last_exec_status_desc: string;

  public last_exec_status_color: string;

 /// <summary>
  ///  啟用註記顏色
  /// <summary>
  public is_active_color: string;
  /// <summary>
  ///  啟用註記
  /// <summary>
  public is_active_desc: string;
  public end_time: string;
  public duration: string;
  public success_step_desc: string;
  public success_step_color: string;
  public fail_step_desc: string;
  public fail_step_color: string;
  public is_table_log: boolean;
  public log_color:string;
  public exec_group:string;
}



